}

.alumni .modal-body{
  padding: 0 !important;
}

.alumni .fix_btn {
  position: fixed;
  bottom: 40px;
  right: 20px;
  z-index: 1;
}


.alumni .add_alumni{
  box-shadow: 0px 8px 8px 1px rgba(0, 0, 0, 0.2) !important;
}

.fix_btn button.add_alumni {
  padding: 4px 13px !important;
  border-radius: 40px !important;
  font-size: 3em !important;
  float: right;
}



