	font-size: 78.5%;
}





.team figure.effect-layla {
    background: #18a367;
  }
  
  .team figure.effect-layla img {
    height: 400px;
  }
  
  .team  figure.effect-layla figcaption {
    padding: 3em;
  }
  
  .team  figure.effect-layla figcaption::before,
  .team  figure.effect-layla figcaption::after {
    position: absolute;
    content: '';
    opacity: 0;
  }
  
  .team  figure.effect-layla figcaption::before {
    top: 50px;
    right: 30px;
    bottom: 50px;
    left: 30px;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    transform: scale(0,1);
    transform-origin: 0 0;
  }
  
  .team figure.effect-layla figcaption::after {
    top: 30px;
    right: 50px;
    bottom: 30px;
    left: 50px;
    border-right: 1px solid #fff;
    border-left: 1px solid #fff;
    transform: scale(1,0);
    transform-origin: 100% 0;
  }
  
  .team figure.effect-layla h2 {
    padding-top: 26%;
    transition: transform 0.35s;
  }
  
  .team figure.effect-layla p {
    padding: 0.5em 2em;
    text-transform: none;
    opacity: 0;
    transform: translate3d(0,-10px,0);
  }
  
  .team figure.effect-layla img,
  .team figure.effect-layla h2 {
    transform: translate3d(0,-30px,0);
  }
  
  .team figure.effect-layla img,
  .team figure.effect-layla figcaption::before,
  .team figure.effect-layla figcaption::after,
  .team figure.effect-layla p {
    transition: opacity 0.35s, transform 0.35s;
  }
  
  .team figure.effect-layla:hover img {
    opacity: 0.7;
    transform: translate3d(0,0,0);
  }
  
  .team figure.effect-layla:hover figcaption::before,
  .team figure.effect-layla:hover figcaption::after {
    opacity: 1;
    background-color: rgb(0, 0, 0, .15);
    transform: scale(1);
  }
  
  .team figure.effect-layla:hover h2,
  .team figure.effect-layla:hover p {
    opacity: 1;
    transform: translate3d(0,0,0);
  }
  
  .team figure.effect-layla:hover figcaption::after,
  .team figure.effect-layla:hover h2,
  .team figure.effect-layla:hover p,
  .team figure.effect-layla:hover img {
    transition-delay: 0.15s;
  }
  


